const chrome = window.chrome;
const KEY = 'chrome-extension-options';
let options;

//localstorage
try {
    options = JSON.parse(localStorage.getItem(KEY)) || {};
} catch (e) {
    options = {};
}

//backgroundwindow
let backgroundWindow;

//try catchgetBackgroundPagecontent scripts
try {
    backgroundWindow = chrome.extension.getBackgroundPage();
} catch(e) {}

export default Object.assign({
    options,

    /*!
     * @desc getsetbackground
     *      browser actioncontent scirpts
     *      content scriptsbom dom
     */
    get(key) {
        return key === undefined ? options : options[key];
    },

    set(key, value) {
        options[key] = value;

        localStorage.setItem(KEY, JSON.stringify(options));

        return options;
    }
}, backgroundWindow === window ? null : {
    /*!
     * @desc content scriptsbrowser action
     *      getAsyncsetAsync, @return  Promise 
     *      getSyncsetSyncwindow
     */
    getAsync(key) {
        return new Promise(resolve => {
            chrome.runtime.sendMessage({
                action: 'getOption',
                key
            }, resolve);
        });
    },
    setAsync(key, value) {
        return new Promise(resolve => {
            chrome.runtime.sendMessage({
                action: 'setOption',
                key,
                value
            }, resolve);
        });
    },

    getSync(key) {
        return backgroundWindow.extensionOptions.get(key);
    },
    setSync(...args) {
        return backgroundWindow.extensionOptions.set(...args);
    }
});



