import React, { Component } from 'react';
import OptionItem from '../OptionItem';
import * as request from '../../utils/request';
import Options from '../../utils/options';
import * as tabUtil from '../../utils/tabUtil';
import InfoItem from '../InfoItem';
import QRcode from 'qrcode';
import './style.scss';

const chrome = window.chrome;

class PageOptions extends Component {
    //
    state = Options.getSync();

    componentWillMount() {
        this.fetchOptions();
        this.getTabInfo();

        //
        //
        chrome.runtime.onMessage.addListener(data => {
            if (data.action === 'active') {
                this.fetchOptions();
            }
        });
    }

    async fetchOptions() {
        //
        const options = await Options.getAsync();
        this.setState(options);
    }

    async setOption(key, value) {
        //background
        const options = await Options.setAsync(key, value);

        //
        //content scripts
        request.sendActiveTabRequest({
            action: 'updateOption',
            options
        });

        request.sendRuntimeMessage({
            action: 'optionUpdated'
        });

        //
        this.setState({
            [key]: value
        });
    }

    async getTabInfo() {
        const allTabs = await tabUtil.getAll();
        const curTab = await tabUtil.getCurrent();
        this.setState({
            curTab: {
                index: curTab.index + 1,
                url: curTab.url,
                title: curTab.title
            }
        });

        this.createQrcode(curTab.url);

        this.setState({
            allTabs: allTabs.length
        });
    }

    createQrcode(text) {
        QRcode.toDataURL(text, {
            color: {
                dark: '#a94442', // Blue dots
                light: '#fff' // Transparent background
            },
        }, (err, qrcode) => {
            this.setState({
                qrcode
            });
        })
    }

    tabUtil = {
        openGithubInNewTab() {
            chrome.tabs.create({
                url: 'https://github.com/qiqiboy'
            });
        },
        async openGithubInCurTab() {
            const tab = await tabUtil.getCurrent();
            chrome.tabs.update(tab.id, {
                url: 'https://github.com/qiqiboy'
            });
        },
        async createBlankTab() {
            const tab = await tabUtil.getCurrent();
            chrome.tabs.create({
                index: tab.index + 1
            });
        },
        async closeCurTab() {
            const tab = await tabUtil.getCurrent();
            chrome.tabs.remove(tab.id);
        },
        createNewWindow() {
            chrome.windows.create();
        },
        closeCurWindow() {
            chrome.windows.getCurrent(null, win => {
                chrome.windows.remove(win.id);
            });
        },
        zoomMoveMaxCurWindow() {
            const screen = window.screen;
            window.close(); //popup

            // 
            chrome.windows.getCurrent(null, win => {
                //
                const steps = [{
                    state: 'normal'
                }, {
                    state: 'minimized'
                }, {
                    state: 'normal'
                }, {
                    width: 500,
                    height: 500,
                    top: screen.availTop,
                    left: screen.availLeft
                }, {
                    top: screen.availTop + screen.availHeight - 500,
                    left: screen.availLeft + screen.availWidth - 500
                }, {
                    state: 'fullscreen'
                }, {
                    state: 'normal'
                }, {
                    state: 'maximized'
                }, {
                    width: win.width,
                    height: win.height,
                    left: win.left,
                    top: win.top,
                    focused: true
                }];

                chrome.runtime.sendMessage({
                    action: 'updateWindow',
                    steps
                });
            });
        }
    }

    render() {
        const { curTab, allTabs, qrcode, ...options } = this.state;

        return (
            <div>
                {curTab && <div className="cur-tab-info">
                    <InfoItem label="" content={curTab.title} />
                    <InfoItem label="" content={curTab.url} />
                    <InfoItem label="" content={`tab${allTabs}${curTab.index}`} />
                    <img src={qrcode} width="100" className="qrcode" alt="qrcode" />
                </div>}
                <OptionItem active={options.enableSkin} onChange={this.setOption.bind(this, 'enableSkin')}>
                    {options.enableSkin ? '' : ''}
                </OptionItem>

                <OptionItem active={options.clearMode} onChange={this.setOption.bind(this, 'clearMode')}>
                    {options.clearMode ? '' : ''}
                </OptionItem>

                <OptionItem active={options.fullscreen} onChange={this.setOption.bind(this, 'fullscreen')}>
                    {options.fullscreen ? '' : ''}
                </OptionItem>

                <div className="btn-group">
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.openGithubInNewTab}>Github</button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.openGithubInCurTab}>Github</button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.createBlankTab}></button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.closeCurTab}></button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.createNewWindow}></button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.closeCurWindow}></button>
                    <button className="btn-option-item btn btn-success" onClick={this.tabUtil.zoomMoveMaxCurWindow}></button>
                    <button className="btn-option-item btn btn-success" onClick={tabUtil.moveCurTabLast}></button>
                    <button className="btn-option-item btn btn-success" onClick={tabUtil.moveCurTabFirst}></button>
                    <button className="btn-option-item btn btn-success" onClick={tabUtil.copyCurWindow}></button>
                </div>

                <OptionItem active={options.animateIcon} onChange={this.setOption.bind(this, 'animateIcon')}>
                    {options.animateIcon ? 'icon' : 'icon'}
                </OptionItem>
            </div>
        );
    }
}

export default PageOptions;



